.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-message {
  min-width: 380px;
  box-sizing: border-box;
  border-radius: 4px;
  border-color: rgba(190, 200, 228, 0.38);
  position: fixed;
  left: 50%;
  top: 20px;
  transform: translateX(-50%);
  background-color: rgba(68, 68, 68, 0.78);
  backdrop-filter: blur(4px);
  color: #ffffff;
  transition: opacity 0.3s, transform 0.4s, top 0.4s;
  overflow: hidden;
  padding: 15px 15px 15px 20px;
  display: flex;
  align-items: center; }
  .tu-message.is-center {
    justify-content: center; }
  .tu-message.is-closable .tu-message__content {
    padding-right: 16px; }
  .tu-message p,
  .tu-message span,
  .tu-message div {
    margin: 0;
    color: #ffffff; }
  .tu-message--info {
    background-color: #f4f4f5;
    border-color: #c5c5c5; }
    .tu-message--info .tu-message__content,
    .tu-message--info .tu-message__icon {
      color: #909399; }
  .tu-message--success {
    background-color: #f0f9ec;
    border-color: #93e769; }
    .tu-message--success .tu-message__content,
    .tu-message--success .tu-message__icon {
      color: #6bc43f; }
  .tu-message--warning {
    background-color: #fdf6ec;
    border-color: #ffc979; }
    .tu-message--warning .tu-message__content,
    .tu-message--warning .tu-message__icon {
      color: #e6a23c; }
  .tu-message--error {
    background-color: #fef0f0;
    border-color: #ff9f9f; }
    .tu-message--error .tu-message__content,
    .tu-message--error .tu-message__icon {
      color: #f56c6c; }
  .tu-message__icon {
    margin-right: 10px; }
  .tu-message__content {
    padding: 0;
    font-size: 14px;
    line-height: 1;
    color: #ffffff; }
    .tu-message__content:focus {
      outline-width: 0; }
  .tu-message__closeBtn {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    cursor: pointer;
    color: #bec8e4;
    font-size: 16px; }
    .tu-message__closeBtn:focus {
      outline-width: 0; }
    .tu-message__closeBtn:hover {
      color: #9baacf; }

.tu-message-fade-enter,
.tu-message-fade-leave-active {
  opacity: 0;
  transform: translate(-50%, -100%); }
